body{background-image:url("https://images.unsplash.com/photo-1518123877527-f4255b0c7795?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1287&q=80");background-repeat:no-repeat;background-position:center;background-size:cover;height:100vh}#name{font-family:'Damion', cursive;font-size:50px;height:25vh;font-weight:lighter;margin:10vw;padding-top:8.5vh;text-align:center;justify-self:center;color:#fff;text-shadow:0 0 7px #ffc8dd,0 0 10px #ffc8dd,0 0 21px #fff,0 0 42px #ffc8dd,0 0 82px #ffc8dd,0 0 92px #ffc8dd,0 0 102px #ffc8dd,0 0 151px #ffc8dd}h1{border:0.2rem solid #fff;border-radius:2rem;text-align:center;padding:0.4;margin-bottom:5vh;box-shadow:0 0 0.2rem #fff,0 0 0.2rem #fff,0 0 2rem #ffc8dd,0 0 0.8rem #ffc8dd,0 0 2.8rem #ffc8dd,inset 0 0 1.3rem #e994aa}.subtitle{background-color:#1C0E2F;opacity:80%;margin:5vw}.subtitle h2{color:#ffc8dd;font-family:'M PLUS Code Latin', sans-serif}.quote{color:#ffc8dd;font-family:'M PLUS Code Latin', sans-serif;font-weight:bolder}

